.header {
    display: flex;
    justify-content: space-between;
    padding: 1em;
    padding-left: 3em;
    padding-right: 3em;
    align-items: center;
}

@media (max-width: 500px) {
    .header {
        flex-wrap: wrap;
        justify-content: space-around;
        padding-left: 5em;
        padding-right: 5em;
    }

    .header-font .header-btn {
        min-width: 45vw;
        max-width: 45vw;
    }
}

.logo-small {
    /* max-height: 50px; */
    max-width: 150px;
    padding-top: 1em;
}

.header-font {
    font-family: var(--ff-primary);
    font-weight: var(--fw-bold);
    /* font-size: 3em; */
    text-decoration: none;
    color: var(--clr-dark);
}

.header-font:hover {
    color: var(--clr-accent);
}

.header-btn {
    display: inline-block;
    padding: 0.2em 2em;
    background: var(--clr-accent);
    color: var(--clr-dark);
    cursor: pointer;
    letter-spacing: 0.5px;
    font-weight: var(--fw-bold);
    border-radius: 0.5em;
    transition: transform 300ms ease-in-out;
    font-family: var(--ff-primary);
}

@media (max-width: 630px) {
    .header-btn {
        padding: 0.2em 1em;
        align-items: center;
    }
}

.header-btn:hover {
    transform: scale(1.1);
}

@media (max-width: 600px) {
    /* .small-font{
        font-size: 1.5em;
    } */

    .logo {
        display: none;
    }

}